import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import List from '@vkontakte/vkui/dist/components/List/List';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import CellButton from '@vkontakte/vkui/dist/components/CellButton/CellButton';
import Header from '@vkontakte/vkui/dist/components/Header/Header';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon28RecentOutline from '@vkontakte/icons/dist/28/recent_outline';


const Home = ({ id, go, fetchedUser }) => (

	<Panel id={id}>
		<PanelHeader>Рт-174</PanelHeader>
		{fetchedUser &&
		<Group title="User Data Fetched with VK Bridge">
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>}
		<Group header={<Header mode="secondary">Расписание</Header>}>
			<CellButton expandable before={<Icon28RecentOutline/>} onClick={ fuction }>
				ПонедельникААААА <Dates number='1'/>
			</CellButton>
			<Cell expandable before={<Icon28RecentOutline/>}>
				Вторник <Dates number='2'/>
			</Cell>
			<Cell expandable before={<Icon28RecentOutline/>}>
				Среда <Dates number='3'/>
			</Cell>
			<Cell expandable before={<Icon28RecentOutline/>}>
				Четверг <Dates number='4'/>
			</Cell>
			<Cell expandable before={<Icon28RecentOutline/>}>
				Пятница <Dates number='5'/>
			</Cell>
			<Cell expandable before={<Icon28RecentOutline/>}>
				Суббота <Dates number='6'/>
			</Cell>
		</Group>
	</Panel>
);

	const Dates = ({ number }) => {
			var weekNumDay = new Date().getDay();		
			var dayDate = new Date().getDate();
			var month = new Date().getMonth() + 1;
			if (month < 10) {
				month = "0" + month;
			}
			var diff = dayDate - (weekNumDay - number);
			return diff.toString() + "." + month.toString();	
	}
	const fuction = async () => {
		console.log('s');
    	try {
      		const response = await fetch('https://radiotech.su/students/fulltimedepartment/Timetable-of-classes/', {
      			method: 'GET',
      			headers: {
        			'Content-Type': 'text/html',
        		}	
      		});
      		if (!response.ok) {
       	 		throw Error(response.statusText);
      		} else {
      			let text = await response.text();
      			console.log(text);
      		}
      		// await console.log(response.text());	
    	} catch (error) {
      		console.log(error);
   	} 
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
